import React, { Component } from 'react';

import { connect } from 'react-redux';

class ResourcesContent extends Component {
    constructor(){
        super() 

        this.state = {
            showContent: true
        }
    }

    componentWillReceiveProps(){
        this.setState({
            showContent: true
        })
        if(this.state.showContent == true && this.props.navbarLinks.length == 6) {
            this.setState({
                showContent: false
            })
        }
    }

    renderContent(){
        let jsx
        if(this.props.navbarLinks){
            this.props.navbarLinks.forEach(link => {
                if(link.active){
                    jsx = link.component
                }
            })
        }
        return jsx
    }

    render(){
        return (
            <div className='resources-content'>
                {
                    this.state.showContent ? 
                    'resources content'
                    :
                    this.renderContent()
                }
            </div>
        )
    }
}

function mapStateToProps(state) {
    const { navbarLinks } = state.headerNavbar
    return { navbarLinks }
}

ResourcesContent = connect(mapStateToProps,null)(ResourcesContent)

export default ResourcesContent