import {
    SET_FILTER_OPTIONS
} from '../actions/types';

const INITIAL_STATE = {
    options: []
}

export default function(state = INITIAL_STATE, action) {
    switch(action.type) {
        case SET_FILTER_OPTIONS:
            const options = action.payload;
            return {
                ...state,
                options
        }
        default: return state;
    }
}