import {
    FETCH_BRANDS,
    FETCH_BRAND_CONTENT,
} from '../actions/types';

const INITIAL_STATAE = {
    brands: [],
    brandcontent: []
}

export default function(state = INITIAL_STATAE, action) {
    switch(action.type){
        case FETCH_BRANDS:
            const brands = action.payload;
            return {
                ...state,
                brands
            }
        case FETCH_BRAND_CONTENT:
            const brandcontent = action.payload;
            return {
                ...state,
                brandcontent
            }
        default: return state
    }
}