    componentWillUnmount(){
        this.props.setNavbarLinks(this.props.navbarLinks)
    }

    renderContent(){
        let jsx
        if(this.props.navbarLinks){
            this.props.navbarLinks.forEach(link => {
                if(link.active){
                    jsx = link.component
                }
            })
        }
        return jsx
    }

    componentWillReceiveProps(){
        this.setState({
            defaultHome: true
        })
        if(this.state.defaultHome == true && this.props.navbarLinks.length == 6) {
            this.setState({
                defaultHome: false
            })
        }
    }

    render() {
        return (
            <div className='home'>
            {
                this.state.defaultHome ? 
                <div className='home__items'>
                    <SubNav className='home__items__subnav' />
                    <HomeFilter className='home__items__filter' />

                    <HomeResources />

                    <HomeEvents />

                    <HomeEnd />
                </div>
                : 
                this.renderContent()
            }
            </div>
        )
    }
