import {
    FETCH_RESOURCES_CONTENT
} from '../actions/types'

const INITIAL_STATE = {
    resources: [],
    resourcespdf: []
}

export default function(state = INITIAL_STATE, action){
    switch(action.type){
        case FETCH_RESOURCES_CONTENT:
            const resourcespdf = action.payload;
            return {
                ...state,
                resourcespdf
            }
        default: return state
    }
}