import React, { Component } from 'react';
import Carousel from 'react-bootstrap/Carousel';

class Slider extends Component {
    render(){
        const { controls,caption,label,images,learnmore } = this.props;
        return (
            <div className='home-slider'>
            <Carousel interval={null} controls={controls}>
                {
                    images.map((image,i) => {
                        return (
                            <Carousel.Item key={i}>
                                <img  className='home-slider__images' src={image} />
                                <Carousel.Caption>
                                    <h3>{caption}</h3>
                                    <p>{label}</p>
                                    <h5>{learnmore}</h5>
                                </Carousel.Caption>
                            </Carousel.Item>
                        )
                    })
                }
            </Carousel>
            </div>
        )
    }
}

export default Slider;