import React , { Component } from 'react'; 
import { connect } from 'react-redux';
import * as actions from '../../actions';
import InputGroup from 'react-bootstrap/InputGroup';
import FormControl from 'react-bootstrap/FormControl';
import Button from 'react-bootstrap/Button';

class HomeEnd extends Component {
    componentDidMount() {
        this.props.fetchLabNotes()
        this.props.fetchFetResources()
    }
    render() {
        const color = ['#65cdca', '#885eb7', '#e2ebf2']
        return(
            <div className='home-end'>
                <div className='home-end__labnotes'>
                    <div className='home-end__labnotes__title'>
                        <h2>HLPC Lab Notes</h2>
                        <p className='view'>View All</p>
                    </div>
                    <div className='home-end__labnotes__line'></div>
                    {
                        this.props.labnotes.map((note,i) => {
                            return <div key={note._id} className='home-end__notes' style={{ borderLeft: `5px solid ${color[i]}`, margin: '5px'}}>
                                <div className='home-end__notes__date' >
                                    {note.dateTime.toString()}
                                </div>
                                <div className='home-end__notes__note'>
                                    {note.title}
                                </div>
                                <div className='home-end__notes__pdf' >
                                    {note.body}
                                </div>
                            </div>
                        })
                    }
                    <div className='home-end__labnotes__subscribe'>
                        <h2>Subscribe to HLPC LabNotes</h2>
                        <label htmlFor='email'>Receive helpful hints and practival advice on HPLC delivered right to your inbox</label>
                        <InputGroup size='sm' style={{width: '400px'}}>
                            <FormControl
                                id='email'
                                placeholder='Email'
                                aria-label='Email'
                                aria-describedby="basic-addon2"
                            />
                            <InputGroup.Append>
                                <Button>SUBSCRIBE</Button>
                            </InputGroup.Append>
                        </InputGroup>
                    </div>
                </div>
                <div className='home-end__resources'>
                    <div className='home-end__resources__title'>
                        <h2 >Featured Resources</h2>
                        <p className='view'>View All</p>
                    </div>
                    <div className='home-end__resources__line'></div>
                    <div className='home-end__resources__pdf'>
                        {
                            this.props.featuredpdf.map(pdf => {
                                return (
                                    <div key={pdf._id}>
                                        <div className='home-end__resources__pdf__image'>
                                            <img src={`${pdf.img}`} />
                                        </div>
                                        <div className='home-end__resources__pdf__fet-pdf'>
                                            {pdf.pdf}
                                        </div>
                                        <Button>View PDF</Button>
                                    </div>
                                )
                            })
                        }
                    </div>
                </div>
            </div>
        )
    }
}

function mapStateToProps(state) {
    const { labnotes,featuredpdf } = state.page
    return { labnotes,featuredpdf }
}

HomeEnd = connect(mapStateToProps,actions)(HomeEnd)

export default HomeEnd;