import React, { Component } from 'react';

import * as actions from '../../actions/brands';
import { connect } from 'react-redux';

import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Image from 'react-bootstrap/Image';

import history from '../../history';

class BrandContent extends Component {
    constructor(){
        super()

        this.state = {
            showContent: false
        }
    }

    componentDidMount(){
        this.props.fetchBrandContent()
    }

    componentWillReceiveProps(){
        this.setState({
            showContent: true
        })
        if(this.state.showContent == true && this.props.navbarLinks.length == 6) {
            this.setState({
                showContent: false
            })
        }
    }

    renderContent(){
        let jsx
        if(this.props.navbarLinks){
            this.props.navbarLinks.forEach(link => {
                if(link.active){
                    jsx = link.component
                }
            })
        }
        return jsx
    }

    render(){
        const brandcontent = this.props.brandcontent
        const { id } = this.props.match.params
        return (
            <Container>
                <Row>
                    {
                        this.state.showContent ? 
                        <div>
                            {
                                brandcontent.map(a =>{
                                    return (
                                        <Col key={a._id}>
                                            <Image src={`${a.contentbanner}`}
                                            onClick={()=> history.push(`/products/brand/content/${id}/${a.slug}`)}
                                            />
                                        </Col>
                                    )
                                })
                            }
                        </div>
                        :
                        this.renderContent()
                    }

                </Row>
            </Container>
        )
    }
}

function mapStateToProps(state) {
    const { brandcontent } = state.brands
    const {navbarLinks} = state.headerNavbar
    return { brandcontent,navbarLinks }
}

BrandContent = connect(mapStateToProps,actions)(BrandContent)

export default BrandContent;