import React, {Component} from "react"
import {connect} from 'react-redux'
import * as actions from '../../actions'

import history from '../../history'

import { Dropdown, Container, Col, Row,ButtonGroup,Button } from 'react-bootstrap'

class Navbar extends Component {

    handleOnClick = (link) => {
        this.props.changeNavbarActive(link._id);
        if(this.props.onClick) {
            this.props.onClick(link._id);
        }
    }
    render() {
        return (
            <div className='navbar'>
                {
                    this.props.navbarLinks.map((link, index) => {
                        return (
                            <div className={`navbar__link ${link.active ? 'green-text' : ''}`} key={index} onClick={()=> this.handleOnClick(link)}>
                                {/* {link.title} */}
                                {
                                    link.isDropDown ? 
                                        <Dropdown as={ButtonGroup}>
                                            <Button>{link.title}</Button>
                                            <Dropdown.Toggle split href={`#`} className={` navbar__dropdown navbar__link ${link.active ? 'green-text' : ''}`}></Dropdown.Toggle>
                                                <Dropdown.Menu>
                                                    <Container>
                                                    {
                                                        link.dropdownItems.map(a => {
                                                            return (
                                                                <Col key={a._id}>
                                                                    <Row>
                                                                        <Dropdown.Item  onClick={() => history.push(`${a.path}`)}>{a.name}</Dropdown.Item>
                                                                    </Row>
                                                                </Col>
                                                            )
                                                        })
                                                    }
                                                    </Container>
                                                </Dropdown.Menu>
                                        </Dropdown>
                                    : link.title
                                }
                            </div>
                        )
                    })                    
                }
            </div>
        )
    }
}

function mapStateToProps(state) {
    const { navbarLinks, onClick } = state.headerNavbar
    return {
        navbarLinks,
        onClick
    }
}

Navbar = connect(mapStateToProps, actions)(Navbar)

export default Navbar;